// hero part start
.section-hero {
    position: relative;
    z-index: 1;
    display: block;
    width: 100%;
    margin-top: -70px;
    overflow: hidden;
    @media (min-width: 1200px) {
        margin-top: -150px;
        aspect-ratio: 21/9;
        max-height: 70vh;
    }

    @media (min-width: 1400px) {
        margin-top: -160px;
    }

    &-content {
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        pointer-events: none;

        @media (min-width: 720px) and (max-width: 1199.98px) {
            margin-top: -16px;
        }

        @media (min-width: 1200px) {
            height: 100%;
        }

        & > img {
            position: relative;
            z-index: -1;
            flex: 1;
            width: 100%;
            height: 100%;
            object-fit: cover;
            @media (min-width: 1200px) {
                position: absolute;
                top: 0;
                left: 0;
            }
        }

        & > .container {
            position: relative;
            display: flex;
            flex-direction: column;
            height: 100%;
            align-items: flex-start;
            justify-content: flex-start;
            row-gap: 15px;
            padding: 0;

            @media (min-width: 1200px) {
                padding: 180px 0 20px;
            }

            & > .breadcrumbs {
                display: none;
                @media (min-width: 1200px) {
                    display: flex;
                }
            }

            & > .btn {
                position: absolute;
                top: -170px;
                left: 50%;
                white-space: nowrap;
                pointer-events: all;
                transform: translate(-50%, 100%);

                @media (min-width: 720px) {
                    display: none;
                }
            }
        }
    }
}

.card-advantage {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 8px;
    column-gap: 8px;
    width: 100%;
    height: auto;
    min-height: 200px;
    padding: 24px;
    opacity: 1;
    background-color: #eef5fe;
    border-radius: 10px;
    backdrop-filter: blur(7.95px);
    border: 1px solid #eef5fe;
    pointer-events: all;
    transition: opacity 0.4s ease;

    @media (min-width: 720px) {
        align-items: flex-start;
        justify-content: flex-start;
        row-gap: 10px;
        column-gap: 10px;
        min-height: 265px;
    }

    @media (min-width: 1200px) {
        background-color: rgba(255, 255, 255, 0.7);
        border: 1px solid rgba(255, 255, 255, 0.7);
        max-width: 40%;
        min-height: 235px;
    }

    @media (min-width: 1400px) {
        padding: 50px 40px;
    }

    & .card-title {
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 28px;
        text-align: center;
        color: var(--blue);

        @media (min-width: 720px) {
            font-size: 36px;
            line-height: 42px;
            text-align: left;
        }
    }

    & p {
        margin: unset;
        font-style: normal;
        font-weight: 300;
        text-align: center;
        flex: 1;

        @media (min-width: 720px) {
            text-align: left;
        }
    }
}

// hero part end

// section prices part start
.section-prices {
    position: relative;
    z-index: 1;
    width: 100%;
    padding: 20px 0 50px;
    @media (min-width: 720px) {
        padding: 20px 0 80px;
    }
    @media (min-width: 1200px) {
        padding: 20px 0 100px;
    }
    & table {
        width: 100%;
        border-collapse: collapse;
        & tr {
            &:nth-child(odd) {
                background-color: #eef5fe;
            }
            & td {
                color: #585a60;
                padding: 17px 20px;
                @media (min-width: 720px) {
                    padding: 17px 50px;
                }
                @media (min-width: 1200px) {
                    padding: 17px 84px;
                }
                & a {
                    color: inherit;
                    &:hover {
                        text-decoration: none;
                    }
                }
                &:last-of-type {
                    text-align: right;
                    white-space: nowrap;
                }
            }
        }
    }
}
// section prices part end

// section doctors part start
.section-doctors {
    position: relative;
    padding: 40px 0;
    background-color: rgba(17, 118, 234, 0.07);
    overflow: hidden;

    @media (min-width: 720px) {
        padding: 63px 0 42px;
    }

    @media (min-width: 1200px) {
        padding: 68px 0 73px;
    }

    & .swiper-doctors {
        position: relative;
        z-index: 1;
        margin-top: 18px;

        @media (min-width: 720px) {
            margin-top: 31px;
        }

        @media (min-width: 1200px) {
            margin-top: 22px;
        }

        & .swiper-container {
            position: relative;
            z-index: 1;

            @media screen and (max-width: 1199.98px) {
                &::before {
                    content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    z-index: 10;
                    border: 0;
                    width: 20%;
                    height: 100%;
                    border-radius: 0;
                    background: linear-gradient(90deg, rgba(17, 118, 234, 0.35) 0%, rgba(255, 255, 255, 0.35) 100%);
                    backface-visibility: hidden;
                }

                &::after {
                    content: "";
                    position: absolute;
                    top: 0;
                    right: 0;
                    z-index: 10;
                    border: 0;
                    width: 20%;
                    height: 100%;
                    border-radius: 0;
                    background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(17, 118, 234, 0.35) 100%);
                    backface-visibility: hidden;
                }
            }
        }

        & .swiper-wrapper {
            width: fit-content;
            margin: 0 auto;
        }

        & .swiper-slide {
            position: relative;
            z-index: 1;
            width: fit-content !important;
        }

        & .swiper-pagination {
            position: relative;
            top: unset;
            bottom: unset;
            left: unset;
            margin-top: 26px;

            @media (min-width: 720px) {
                margin-top: 36px;
            }

            @media (min-width: 1200px) {
                margin-top: 40px;
            }
        }
    }
}

.card-doctor {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    border-radius: 10px;
    padding-bottom: 25px;
    overflow: hidden;
    transition: 0.4s ease;
    max-width: 280px;
    text-decoration: none;

    @media (min-width: 720px) {
        width: 340px;
        max-width: 100%;
        padding-bottom: 17px;
    }

    &-image {
        position: relative;
        z-index: 1;
        width: 100%;
        height: 100%;
        aspect-ratio: 1 / 1.25;
        border-radius: 10px;
        overflow: hidden;

        & > * {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    & p {
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        color: var(--blue);
        margin: 0;
        padding: 25px 20px 0 20px;

        @media (min-width: 720px) {
            font-size: 18px;
            line-height: 22px;
        }

        @media (min-width: 1200px) {
            font-size: 22px;
            line-height: 27.5px;
            padding: 30px 20px 0 20px;
        }
    }

    &-meo {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #585a60;
        padding: 4px 20px 0 20px;

        @media (min-width: 720px) {
            font-size: 16px;
            line-height: 20px;
        }
    }

    &-experience {
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 18px;
        color: var(--blue);
        padding: 18px 20px 0 20px;

        @media (min-width: 720px) {
            font-size: 18px;
            line-height: 22.5px;
        }
    }

    &:hover {
        box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.25);
    }
}

// section doctors part end

// section smile part start
.section-smile {
    position: relative;
    z-index: 1;
    padding: 50px 0;
    @media (min-width: 720px) {
        padding: 80px 0;
    }
    @media (min-width: 1200px) {
        padding: 100px 0;
    }
    overflow: hidden;

    & .container {
        display: flex;
        flex-direction: column;
        align-items: center;

        & .btn {
            width: 100%;
            margin-top: 50px;

            @media (min-width: 576px) {
                width: 390px;
            }

            @media (min-width: 720px) {
                margin-top: 55px;
            }

            @media (min-width: 1200px) {
                width: 31vw;
            }

            @media (min-width: 1680px) {
                width: 517px;
            }
        }
    }

    & .swiper-smiles {
        margin-top: 16px;

        @media (min-width: 720px) {
            margin-top: 34px;
        }

        @media (min-width: 1200px) {
            margin-top: 40px;
        }

        & .swiper-wrapper {
            width: fit-content;
            margin: 0 auto;
        }

        & .swiper-slide {
            position: relative;
            z-index: 1;
            width: fit-content !important;
        }

        & .swiper-pagination {
            position: relative;
            top: unset;
            bottom: unset;
            left: unset;
            margin-top: 26px;

            @media (min-width: 720px) {
                margin-top: 36px;
            }

            @media (min-width: 1200px) {
                margin-top: 40px;
            }
        }
    }
}

.smile-card {
    position: relative;
    z-index: 1;
    display: block;
    height: 100%;
    width: 94vw;
    @media (min-width: 720px) {
        width: 54vw;
    }

    @media (min-width: 1200px) {
        width: 31vw;
    }

    @media (min-width: 1680px) {
        width: 517px;
    }

    & .smile-title {
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 18px;
        color: #585a60;

        @media (min-width: 720px) {
            font-size: 22px;
            line-height: 27.5px;
        }
    }

    & .smile-images {
        position: relative;
        z-index: 5;
        width: 100%;
        aspect-ratio: 2 / 1.25;
        pointer-events: all;
        margin-top: 10px;

        @media (min-width: 720px) {
            aspect-ratio: 2 / 1.5;
        }

        & img {
            object-fit: cover;
        }

        & img-comparison-slider {
            visibility: hidden;
            outline: none;
            border: 0;

            & [slot="second"] {
                display: none;
            }

            &.rendered {
                visibility: inherit;
                width: 100%;
                height: 100%;

                & [slot="first"],
                & [slot="second"] {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }

                & [slot="second"] {
                    display: unset;
                }
            }
        }

        & *:focus-visible {
            outline: none;
        }
    }

    & .smile-name {
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: var(--blue);
        margin-top: 3px;
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }

        @media (min-width: 720px) {
            font-size: 20px;
            line-height: 25px;
        }
    }

    & .smile-doctor {
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #585a60;
        margin-top: 18px;

        @media (min-width: 720px) {
            font-size: 16px;
            line-height: 20px;
            margin-top: 6px;
        }
    }
}

// section smile part end

// section result part start
.section-result {
    position: relative;
    z-index: 1;
    display: block;
    padding: 67px 0 37px;
    overflow: hidden;

    @media (min-width: 720px) {
        padding: 70px 0 60px;
    }

    @media (min-width: 1200px) {
        padding: 70px 0 80px;
    }
}

.result-list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    row-gap: 8px;
    column-gap: 8px;
    @media (min-width: 720px) {
        row-gap: 20px;
        column-gap: 20px;
    }
    @media (min-width: 992px) {
        grid-template-columns: repeat(3, 1fr);
    }
    @media (min-width: 1200px) {
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: auto;
    }
}

.results-image {
    display: none;
    flex: 1;
    width: 30%;
    max-width: 629px;
    border-radius: 14px;
    overflow: hidden;

    @media (min-width: 1200px) {
        display: flex;
        flex-direction: column;
    }

    & > * {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.result-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding: 20px 5px 30px;
    border: 1px solid rgba(44, 53, 109, 0.3);
    border-radius: 10px;
    background: #ffffff;

    @media (min-width: 720px) {
        padding: 38px 12px 30px;
    }

    @media (min-width: 1200px) {
        padding: 45px 10% 30px;
    }
    @media (min-width: 1400px) {
        padding: 45px 15% 30px;
    }

    & svg {
        width: fit-content;
        height: 53px;
        object-fit: contain;
        margin-bottom: 13px;

        @media (min-width: 720px) {
            height: 75px;
        }
    }

    & p {
        margin: unset;
        font-size: 12px;
        line-height: 15px;
        text-align: center;

        @media (min-width: 720px) {
            font-size: 16px;
            line-height: 20px;
        }
    }
}

// section result part end

// section consult part start
.section-consult {
    position: relative;
    z-index: 1;
    padding: 40px 0;
    background: linear-gradient(180deg, #1176ea 0%, rgba(17, 118, 234, 0.8) 100%);

    @media (min-width: 720px) {
        padding: 60px 0;
    }

    @media (min-width: 1200px) {
        padding: 50px 0;
        background-image: linear-gradient(195deg, var(--blue) 0%, #1b66bd 100%);
    }

    & h2 {
        color: #ffffff;
    }

    & .consult-form {
        padding: 0 8.335%;

        @media screen and (max-width: 1199.98px) {
            & > * {
                &:nth-child(1) {
                    order: -1;
                }

                &:nth-child(2) {
                    order: 1;
                }

                &:nth-child(3) {
                    order: 5;
                }

                &:nth-child(4) {
                    order: 0;
                }

                &:nth-child(5) {
                    order: 4;
                }

                &:nth-child(6) {
                    order: 5;
                }

                &:nth-child(7) {
                    order: 6;
                }
            }
        }

        @media (min-width: 720px) {
            padding: 0 16.665%;
        }

        @media (min-width: 1200px) {
            padding: 0;
        }
    }
}

.consult-form {
    display: flex;
    flex-direction: column;
    column-gap: 5px;
    row-gap: 5px;
    margin-top: 24px;

    @media (min-width: 720px) {
        column-gap: 12px;
        row-gap: 12px;
        margin-top: 40px;
    }

    @media (min-width: 1200px) {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, auto);
        column-gap: 8px;
        row-gap: 8px;

        & > * {
            &:nth-child(1) {
                grid-area: 1 / 1 / 2 / 2;
            }

            &:nth-child(2) {
                grid-area: 1 / 2 / 2 / 3;
            }

            &:nth-child(3) {
                grid-area: 1 / 3 / 3 / 4;
            }

            &:nth-child(4) {
                grid-area: 2 / 1 / 3 / 2;
            }

            &:nth-child(5) {
                grid-area: 2 / 2 / 3 / 3;
            }

            &:nth-child(6) {
                grid-area: 3 / 2 / 4 / 4;
            }
        }
    }

    & .input-textarea {
        resize: none;
    }

    &-agree {
        @media (min-width: 1200px) {
            align-self: center;
        }

        & * {
            color: #ffffff;
        }
    }

    &-link {
        text-decoration: underline;
        text-decoration-skip-ink: none;

        &:hover {
            text-decoration: none;
        }
    }

    & .btn {
        width: fit-content;
        padding: 16px 23%;
        margin: auto;

        @media (min-width: 720px) {
            padding: 20px 20%;
        }

        @media (min-width: 1200px) {
            margin: unset;
            padding: 20px 136px;
        }
    }

    &-submit {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        column-gap: 15px;
        row-gap: 15px;
        margin-top: 15px;

        @media (min-width: 720px) {
            column-gap: 35px;
            row-gap: 35px;
            margin-top: 7px;
        }

        @media (min-width: 1200px) {
            flex-direction: row;
            align-items: center;
            justify-content: flex-end;
            column-gap: 24px;
            row-gap: 24px;
            margin-top: 16px;
        }
    }
}

// section consult part end
